<template>
    <div class="content-bg" v-loading.fullscreen.lock="fullscreenLoading" element-loading-text="获取中"  element-loading-background="rgba(0, 0, 0, 0.6)">
      <div class="top-title">资产流量 </div>
      <div style="padding: 0 5px;"><v-basedate class="from-wapper" busName="equipmentFlow2DateBus2"></v-basedate></div>
      <div class="item-wapper">
        <div class="item-title">资产（IP）数据包个数</div>
        <div class="item-content">
          <v-echarts echartType="bar" :echartData = "this.ipPacketsData" ></v-echarts>
        </div>
      </div>
      <div class="item-wapper">
        <div class="item-title">资产（IP）数据包个数</div>
        <div class="item-content">
          <v-echarts echartType="pie" :echartData = "this.ipPacketsData2" ></v-echarts>
        </div>
      </div>
      <div class="item-wapper">
        <div class="item-title">资产（服务）数据包个数</div>
        <div class="item-content">
          <v-echarts echartType="bar" :echartData = "this.domainPacketsData" ></v-echarts>
        </div>
      </div>
      <div class="item-wapper">
        <div class="item-title">资产（服务）数据包个数</div>
        <div class="item-content">
          <v-echarts echartType="pie" :echartData = "this.domainPacketsData2" ></v-echarts>
        </div>
      </div>
    </div>
    
</template>

<script>
    import vBasedate from '../common/baseDate'
    import vEcharts from '../common/echarts'
    import bus from '../common/bus';
    import {dateFormat} from '../../../static/js/common'
    export default {
        name: "equipmentFlow2",
        data() {
            return {
                fullscreenLoading:false,
                interTime:'',
                //刷新时间间隔
                refreshIntTime :'5000',
                //数据日期间隔
                dataTime:3600,
                //资产（ip）数据包个数
                ipPacketsData:{
                    baseConfig:{
                        title:'',
                        xAxisName:'资产\n(IP)',
                        yAxisName:'数据包/个',
                        rotate:'20',
                        itemColor:['rgba(68,47,148,0.5)','rgba(15,219,243,1)']
                    },
                    xAxisArr:[],
                    yAxisArr:[]
                },
                ipPacketsData2:{
                    baseConfig:{
                        title:'',
                        hoverText:'百分比'
                    },
                    xAxisArr:[],
                    yAxisArr:[]
                },
                //资产（域名）数据包个数
                domainPacketsData:{
                    baseConfig:{
                        title:'',
                        xAxisName:'资产\n(服务)',
                        yAxisName:'数据包/个',
                        rotate:'15',
                        itemColor:['rgba(68,47,148,0.5)','rgba(15,219,243,1)']
                    },
                    xAxisArr:[],
                    yAxisArr:[]
                },
                domainPacketsData2:{
                    baseConfig:{
                        title:'',
                        hoverText:'百分比'
                    },
                    xAxisArr:[],
                    yAxisArr:[]
                },
            }
        },
        created(){
            /*监听刷新时间改变*/
            bus.$on('equipmentFlow2DateBus2',(arr)=> {
              let paramObj = {
                starttime: arr[0],
                endtime: arr[1]
              }
              this.fullscreenLoading = true;
              /*获取资产（ip）数据包个数*/
              this.getIpPacketsData(paramObj);
              /*获取资产（域名）数据包个数*/
              this.getDomainPacketsData(paramObj);
            })
        },
        mounted(){},
        methods:{
            /*获取资产（ip）数据包个数*/
            getIpPacketsData(paramObj){
                this.$nextTick(()=>{
                    this.$post('/flow/getDstIPPacketCount.do',paramObj)
                        .then(res=>{
                            this.fullscreenLoading = false;
                            let xns1 = [];
                            let yvs1 = [];
                            let yvs2 = [];
                            for(let i in res){
                                let obj = res[i];
                                for (let j in obj){
                                    xns1.push(j);
                                    yvs1.push(obj[j])
                                    yvs2.push({
                                        name:j,
                                        value:obj[j]
                                    })
                                }
                            }
                            this.ipPacketsData.xAxisArr = xns1;
                            this.ipPacketsData.yAxisArr = yvs1;
                            this.ipPacketsData2.yAxisArr = yvs2;
                        })
                        .catch(err=>{
                          this.fullscreenLoading = false;
                        })
                })
            },
            /*获取资产（域名）数据包个数*/
            getDomainPacketsData(paramObj){
                this.$nextTick(()=>{
                    this.$post('/flow/getDstUrlPacketCount.do',paramObj)
                        .then(res=>{
                            this.fullscreenLoading = false;
                            let xns1 = [];
                            let yvs1 = [];
                            let yvs2 = [];
                            for(let i in res){
                                let obj = res[i];
                                for (let j in obj){
                                    xns1.push(j);
                                    yvs1.push(obj[j]);
                                    yvs2.push({
                                        name:j,
                                        value:obj[j]
                                    })
                                }
                            }
                            this.domainPacketsData.xAxisArr = xns1;
                            this.domainPacketsData.yAxisArr = yvs1;
                            this.domainPacketsData2.yAxisArr = yvs2;
                        })
                        .catch(err=>{
                          this.fullscreenLoading = false;
                        })
                })
            },
        },
        components:{
            vBasedate,
            vEcharts
        }
    }
</script>

<style scoped>
  .content-bg{
    /*height: 1000px;*/
    font-size: 1rem;
    background: url("../../../static/img/flow-index-bg2.png");
    background-size: 100% 100%;
  }
  .top-title{
    font-size: 0.5rem;
    color: #185bff;
    padding: 10px;
  }
  .item-wapper{
    height: auto;
    /*background: rgb(26,36,47);*/
    background: url("../../../static/img/bd2.png");
    background-size: 100% 100%;
    margin:0.5rem 0.2rem;
  }
  .item-title{
    height: 1rem;
    line-height: 1rem;
    font-size: 0.38rem;
    text-align: left;
    padding-left: 25px;
    /*background: url("../../../static/img/title-bg.png");*/
    background-size: 100% 100%;
  }
  .item-content{
    /*height: calc(100% - 1rem);*/
    height: 230px;
    box-sizing: border-box;
    padding: 0.5rem;
    padding-top: 0;
  }
</style>



